export default {
	created() {
		var neirong = this.questionList[this.number-1]
		if(this.tag == "l"){
			neirong = this.questionList[this.qNumber]
		}
		this.content = neirong.content
		this.rightAnswer = neirong.jiandaanswer
		this.jiexi = neirong.jiexi
	},
	data() {
		return {
			content: '',
			jiexi: '',
			fenshu: '',
			flag: false,
			text: '点击显示答案',
			rightAnswer: ''
		};
	},
	methods:{
		show (){
			if(this.text == '点击显示答案'){
				this.text = '点击隐藏答案'
				this.flag = true
			}else{
				this.text = '点击显示答案'
				this.flag = false
			}
		},
		changeQuestion (e){
			var data = {}
			if(this.tag == "l"){
				data = {
					ps: e
				}
			}else {
				if(this.number == this.questionList.length){
					data = {
						ps: e,
						submit: true,
						answer: parseInt(this.fenshu),
						tixing: this.questionList[this.number-1].tixing,
						number: this.number-1
					}
				}else{
					data = {
						ps: e,
						submit: false,
						answer: parseInt(this.fenshu),
						tixing: this.questionList[this.number-1].tixing,
						number: this.number-1
					}
				}
			}
			this.$emit('func', data);	
		}
	},
	props:[
		"number",
		"questionList",
		"nextText",
		"jiexiTag",
		"qNumber",
		"tag"
	]
}
